body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.series-list{
  margin-top: 5em;
  transition: 2s ease-in-out;
  list-style-type: none;
  color: #aaa;
  text-align: center;
  border-radius: 15px;
}

a:hover{
  color: rgb(74, 206, 250);
  transition: 0.2s ease-in-out;
  background-color: rgba(255, 255, 255, 0.589);
  border-radius: 5px;
  padding: 3px;
}

a{
  color: #707070;
  text-decoration: none;
  transition: 2s ease-in-out;
  font-size: 1.1em;
}


ul{
  margin: 0 20vw;
  background-image: linear-gradient(rgba(195, 221, 255, 0.5), rgba(255, 255, 255, 0.8));
}

li{
  padding: 0px;
  transition: 1s ease-in-out;
}

.search-bar input{
  color:rgb(40, 40, 40);
  border: 1px solid rgb(238, 255, 7);
  padding: 5px 10px;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 1.2em;
  transition: 1s ease-in-out;
}
.home-body{
  background-image: url(/static/media/home.09f105a9.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  border-top: 1px solid red;
}

.search-message{
  color: white;
}

.red{
  color: red
}
.seriesInfo{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 200px 1fr 1fr 100px;
  margin: 0 10vw;
  grid-gap: 1em;
  border-left: 1px solid red;
  background-color: #fcfcfc; 
}

.show-name{
  grid-row: 1;
  grid-column: 1/3;
  padding-top: 50px;
}

.show-name h2{
  text-decoration: underline;
}

.show-img{
  grid-column: 1/2;
}

.show-img img{
  border: 5px solid #ffe;
}

.show-details{
  text-decoration: none;
  text-align: left;
  display: block;
}

.series-imdb{
  text-decoration: none;
  background-color: #ededed;
  padding: 0.1em;
  border-radius: 5px;
}
.series-imdb:hover{
  background-color: yellow;
  color: black;
  font-weight: 1000;
  transition: 0.2s ease-in;
}

.series-offLink{
  display: inline-block;
  margin-left: 1em;
  text-decoration: none;
  background-color: #ededed;
  padding: 0.1em;
  border-radius: 5px;
}

.series-offLink:hover{
  transition: 0.2s ease-in;
}


.show-summary{
  padding: 2em 5em 0 5em;
  text-align: left;
  grid-column: 1/3;
  line-height: 1.5em;
}

.link{
  grid-column: 1/3;
  grid-row: 4/5;
}
.link a{
  text-decoration: none;
  background-color: #ededed;
  padding: 0.5em;
  border-radius: 5px;
}
.link a:hover{
  background-color: white;
  border: 1px solid green;
  transition: 0.2s ease-in-out;
  color: black;
}



@media (max-width: 575.98px) {

  .seriesInfo{
    display: block;
    margin: 0 5vw;
  }

  .show-details{
    text-align: center;
  }

  .show-summary{
    margin: 2em 0;
    text-align: center;
    padding: 0 1em;
  }

  .link{
    margin: 2em 0;
  }

}

.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 60px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: 1.2em;
  color: rgb(206, 206, 206);
}

.App-footer{
  border-top: 1.5px solid red;
  background-color: black;
  color: white;
  padding: 1em
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

